.search-bar{
    width: 90%;
    height: 50px;
    margin: 0px auto;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
}
.search-input{
    margin: 0px 10px;
    border: 0px;
    height: 100%;
    width: 100%;
    padding: 0px;
    font-size: 18px;
}

.search-input:focus{
    outline: none;
}

.search-icon{
    padding: 0px 10px;
}

.poster-banner{
    width: 104px;
    margin-right: 7px;
    padding-bottom: 10px;

}

.poster-banner>img{
    display: block;
    height: 180px;
    width: 104px;

}

.poster-banner p{
    margin: 0px;
    line-height: 20px;
    height: 40px;
    color: white;
    width: 100%;
    font-size: 13px;
    overflow: hidden;
}

.search-result-wrapper{
    display: flex;
}