.home-banner{
    height: 220px;
    width: 100%;  
}

.home-banner img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.home-banner p{
    color: white;
    font-size: 30px;
    margin: 10px 0px;
    font-weight: 700;
}


.loading-wrapper{
    height: 100%;
    width: 100%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading-wrapper>ul{
    padding: 0px;
    list-style: none;
    text-align: center;
    font-size: 20px;
}

.loading-wrapper>ul p{
    font-size: 20px;
}