.category-card{
    padding-left: 20px;
}

.category-card>p{
    color: white;
    font-weight: 700;
    font-size: x-large;
}

.scroll-view-wrapper>a>div{
    height: 180px;
    width: 104px;
    margin-right: 7px;
    padding-bottom: 10px;
}

.scroll-view-wrapper>a>div>img{
    height: 140px;
    width: 104px;
    border-radius: 5px;
}

.scroll-view-wrapper p{
    margin: 0px;
    line-height: 20px;
    height: 40px;
    color: white;
    width: 100%;
    font-size: 13px;
    overflow: hidden;
}

.scroll-view-wrapper{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;

}

a{
    text-decoration: none;
}