.anime-banner{
    height: 300px;
    width: 100%;
    position: relative;
}

.anime-banner>img{
    height: 300px;
    width: 100%;
    object-fit: cover;
}

.banner-shadow{
    height: 300px;
    z-index: 2;
    position: absolute;
    width: 100%;
    transform: translateY(-100%);
    background: linear-gradient(0deg, rgb(11, 9, 10) 25%, rgba(0,0,0,0.5) 60%, rgba(0,0,0,0) 100%);
    display: flex;
    align-items: flex-end;
}

.anime-title{
    color: white;
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    width: 100%;
}

.episode-break{
    color: white;
    font-size: 30px;
    padding-left: 20px;
    font-weight: 700;
}

.episode-wrapper{
    width: 90%;
    height: 50px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px auto;
    border-radius: 5px;
}

.episode-wrapper p{
    padding: 0px 5px;
    text-align: center;
    color: black;
}

.anime-plot{
    color: white;
    padding: 0px 20px;
    text-align: center;
}